import fetch from 'isomorphic-fetch';
import Error from 'next/error';
import Layout from '../components/Layout';

class Story extends React.Component {
    
    static async getInitialProps({ req, res, query }) {
        
        const storyId = query.id;
        let content;
        try {
            const response = await fetch(`https://node-hnapi.herokuapp.com/item/${storyId}`);
            content = await response.json();
        } catch ( err ) {
            content = [];
        }
        return ( { content });
    }
    
    render() {

        const { content } = this.props;

        if ( !content ) {
            return <Error statusCode={503} />
        }

        return (
            <Layout title={content.title}>
                <main>
                    <h1><a href={content.url}>{content.title}</a></h1>
                    <div className="story-details">
                        <strong>{content.points}</strong>
                        <strong>{content.comment_count}</strong>
                        <strong>{content.time_ago}</strong>
                    </div>
                </main>
            </Layout>
        )
    }
}
export default Story;